import { addLineAndCopy } from "../assets/js/lineAndCopy";
import NumberGrow from "./NumberGrow";

export default {
  components: {
    NumberGrow
  },
  data() {
    return {
      codeHtml: `<pre><code class="language-javascript">const changeOwn = (study = false，days = 365) =&gt; {
    let everyDay = 1
    let own = 0
    everyDay = study ? everyDay + 0.01 : everyDay - 0.01
    own = Math.pow(everyDay, days)
    return own
}</code></pre>`,
      isStudy: false,
      number: 0,
      days: 365,
      load: true
    };
  },
  created() {},
  mounted() {
    addLineAndCopy();
    this.changeOwn(this.isStudy,this.days)
  },
  methods: {
    changeOwn(study = false, days = 365) {
      console.log(study, days)
      let everyDay = 1;
      let own = 0;
      everyDay = study ? everyDay + 0.01 : everyDay - 0.01;
      own = Math.pow(everyDay, days);
      console.log(own);
      this.number = own;
      this.load = false;
      this.$nextTick(() => {
        this.load = true;
      });
    }
  }
};
